<template>
  <div class="detail-box">
    <DetailNavBar :current="currentIndex" @doIndex="doIndex"/>
    <ScrollComponent ref="scroll" class="scroll" @doTabFixed="doTabFixed" @doCanBackTop="doCanBackTop">
      <WrapperComponent :list="banner"/>

      <div id="param" style="height: 500px;background-color: #ff4466">
        参数
      </div>
      <div id="comment" style="height: 300px;background-color: aqua">
        评论
      </div>
      <div id="recommend" style="height: 600px;background-color: pink">
        推荐
      </div>
    </ScrollComponent>
    <BackTopComponent v-show="backTopShow" @click.native="doBackTop"></BackTopComponent>
    <DetailTabBar @doAddToCart="doAddToCart"></DetailTabBar>
  </div>
</template>

<script>
import DetailNavBar from "@/views/detail/children/DetailNavBar";
import WrapperComponent from "@/components/common/wrapper/WrapperComponent";
import homeService from "@/services/homeService";
import ScrollComponent from "@/components/common/scroll/ScrollComponent";
import BackTopComponent from "@/components/common/backTop/BackTopComponent";
import {backTop} from "@/common/mixin";
import DetailTabBar from "@/views/detail/children/DetailTabBar";
import config from '@/config/global.config.js'

export default {
  name: "Detail",
  data() {
    return {
      id: 0,
      banner: [],
      offsets: [],
      currentIndex: 0
    }
  },
  components: {
    DetailNavBar,
    WrapperComponent,
    ScrollComponent,
    BackTopComponent,
    DetailTabBar,
  },
  created() {
    this.banner = homeService.getBanner()
  },
  activated() {
    this.id = parseInt(this.$route.params.id)
  },
  mounted() {
    const offsets = [0]
    offsets.push(document.getElementById('param').offsetTop)
    offsets.push(document.getElementById('comment').offsetTop)
    offsets.push(document.getElementById('recommend').offsetTop)
    this.offsets = offsets
  },
  mixins: [backTop],
  methods: {
    doAddToCart() {
      const product = {}
      product.id = this.id
      product.title = "【送宝宝凳】【顺丰】皓冰 儿童床拼接实木男孩女孩婴儿床带护栏小孩床单人床加宽床拼接床 加厚 三面尾梯 150*70*40实木床（店长推荐）"
      product.image = config.imgPath + 'tab.jpg'
      product.price = '128.00'
      product.desc = '儿童床拼接实木男孩女孩婴儿床带护栏小孩床'
      product.checked = true

      // 将商品加入购物车
      this.$store.dispatch('doAddCart',product).then((res)=>{
        this.$toast.toast(res,3000)
      })
    },
    doIndex(index) {
      this.$refs.scroll.scroll.scrollTo(0, -this.offsets[index] + 44, 300)
    },
    doTabFixed(y) {
      y = -y
      const len = this.offsets.length
      for (let i in this.offsets) {
        i = parseInt(i)


        if (i < len - 1) {

          if (this.offsets[i] < y && y <= this.offsets[i + 1]) {

            if (this.currentIndex === i) {
              return
            }
            this.currentIndex = i
          }
        } else {
          if (y >= this.offsets[i]) {
            if (this.currentIndex === i) {
              return
            }
            this.currentIndex = i
          }
        }

      }
    }
  }
}
</script>

<style scoped>
.detail-box {
  width: 100%;
  height: 100%;
}

.scroll {
  height: calc(100vh - 44px);
  overflow: hidden;
}
</style>
