<template>
  <NavBarComponent class="box">
    <img @click="doGoBack" class="left" slot="left" src="@/assets/img/arrow-left.svg" alt="arrow-left">
    <div slot="center" class="center">
      <div @click="doWitchTab(index)" :class="currentIndex===index?'active':''" v-for="(item,index) in list"
           :key="item">{{ item }}
      </div>
    </div>
  </NavBarComponent>
</template>

<script>
import NavBarComponent from "@/components/common/navBar/NavBarComponent";

export default {
  name: "DetailNavBar",
  props:{
    current:{
      required:true,
      type:Number
    }
  },
  data() {
    return {
      list: [
        '商品',
        '参数',
        '评论',
        '推荐'
      ],
      currentIndex: 0
    }
  },
  watch:{
    current(){
      this.currentIndex = this.current
    }

  },
  components: {
    NavBarComponent
  },
  methods: {
    doWitchTab(index) {
      this.currentIndex = index
      this.$emit('doIndex',index)
    },
    doGoBack() {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.box {
  box-shadow: 0px 0px 5px rgba(100, 100, 100, 1);
}

.center {
  display: flex;
}

.center div {
  flex: 1;
}

.left {
  width: 32px;
  height: 32px;
}

.active {
  color: var(--color-tint);
}
</style>
